import React from "react";
import "./Changelog.css";
import changelog from "./assets/changelog.png";
import Logo from "../../../common/logo/Logo";

function Changelog() {
  return (
    <header className="app-header">
      <div className="app-container features-container">
        <div className="logo-box" style={{ marginBottom: "3rem" }}>
          <Logo navigateTo={"/GitVersioniser/features"} />
        </div>
        <div className="container" style={{ width: '100%', marginBottom: '3rem'}}>
          <div className="row flex">
            <div className="col-sm-12" style={{ alignSelf: 'center' }}>
              <h1>
                Automatic Changelog creation.
              </h1>
              <br/>
              <p>
                New entries are created based on the commit tags in the commit
                message.
              </p>
            </div>
          </div>
        </div>
        <div className="container">
          <div className="row flex">
            <div className="col-sm-12">
              <img
                src={changelog}
                alt="Git Versioniser Logo"
                className="feature-image"
                style={{ width: "19em" }}
              />
            </div>
          </div>
        </div>
      </div>
    </header>
  );
}

export default Changelog;
